.chat {
  height: 100vh;
  justify-content: center;
  align-items: stretch;
  display: flex;
  overflow: hidden;
}

.chat .chat__menu {
  max-width: 310px;
  width: 100%;
  min-height: 100vh;
  background-color: #add8e6;
  overflow-y: scroll;
}

.chat .chat__messages {
  max-width: calc(100% - 310px);
  width: 100%;
  min-height: 100vh;
  align-items: space-between;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.messages__header {
  height: 54px;
  background-color: #90ee90;
  justify-content: flex-start;
  align-items: center;
  gap: 0 10px;
  display: flex;
}

.messages__wrapper {
  height: calc(100% - 86px);
  background-color: #ffffe0;
  flex-direction: column;
  justify-content: flex-start;
  display: flex;
  overflow-y: scroll;
}

.messages__wrapper .message__item {
  text-align: left;
  width: 100%;
}

.messages__wrapper .message__item.--self {
  text-align: right;
}

.messages__wrapper .message__item > div {
  max-width: 50%;
  width: auto;
  display: inline-block;
}

.messages__footer {
  background-color: #90ee90;
  justify-content: space-between;
  align-items: flex-end;
  gap: 0 10px;
  display: flex;
}

.messages__footer-msg {
  width: 100%;
  justify-content: space-between;
  align-items: flex-end;
  gap: 0 10px;
  display: flex;
}

.messages__footer .msg__textarea {
  width: 100%;
  resize: none;
}

.messages__footer .msg__submit {
  width: 28px;
}



h1 {
  color: green;
}

